// src/app/categoria/[slug]/page.js
'use client';

import { useParams } from 'next/navigation';
import products from '@/data/products.json';
import categories from '@/data/categories.json';
import ProductCard from '@/components/ProductCard';
import Link from 'next/link';
import Image from 'next/image';
import { useState } from 'react';

export default function CategoriaPage() {
  const params = useParams();
  const slug = params.slug;

  // Encontrar a categoria e seus produtos
  const category = categories.find((c) => c.slug === slug);
  const categoryProducts = products.filter((p) => p.category === slug);

  // Estados para filtro e ordenação
  const [filter, setFilter] = useState('all');
  const [sort, setSort] = useState('default');

  // Função para aplicar filtros
  const filteredProducts = categoryProducts.filter((product) => {
    if (filter === 'all') return true;
    return product.type?.toLowerCase() === filter.toLowerCase();
  });

  // Função para ordenar produtos
  const sortedProducts = [...filteredProducts].sort((a, b) => {
    if (sort === 'price_asc') return a.price - b.price;
    if (sort === 'price_desc') return b.price - a.price;
    return 0; // padrão
  });

  return (
    <main className="min-h-screen p-4 bg-gray-100">
      <section className="mb-8 text-center">
        <h1 className="text-4xl font-extrabold text-gray-800 mb-2">
          {category?.name || 'Categoria'}
        </h1>
        <p className="text-lg text-gray-600">{category?.description}</p>
        {category?.image && (
          <div className="mt-4 rounded-lg overflow-hidden shadow-lg max-w-md mx-auto">
            <Image
              src={category.image}
              alt={category.name}
              width={600}
              height={300}
              layout="responsive"
              objectFit="cover"
              className="rounded-lg"
            />
          </div>
        )}
      </section>

      {/* Filtros e Ordenação */}
      <section className="mb-6 flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div className="flex gap-2">
          <label htmlFor="filter">Filtrar por tipo:</label>
          <select
            id="filter"
            value={filter}
            onChange={(e) => setFilter(e.target.value)}
            className="border border-gray-300 rounded px-2 py-1"
          >
            <option value="all">Todos</option>
            <option value="lager">Lager</option>
            <option value="ipa">IPA</option>
            <option value="stout">Stout</option>
            <option value="ale">Ale</option>
            {/* Adicione mais tipos conforme necessário */}
          </select>
        </div>

        <div className="flex gap-2">
          <label htmlFor="sort">Ordenar por:</label>
          <select
            id="sort"
            value={sort}
            onChange={(e) => setSort(e.target.value)}
            className="border border-gray-300 rounded px-2 py-1"
          >
            <option value="default">Padrão</option>
            <option value="price_asc">Preço: Menor → Maior</option>
            <option value="price_desc">Preço: Maior → Menor</option>
          </select>
        </div>
      </section>

      {/* Grid de Produtos */}
      <section className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
        {sortedProducts.length > 0 ? (
          sortedProducts.map((product) => (
            <ProductCard key={product.id} product={product} />
          ))
        ) : (
          <p className="text-gray-600 col-span-full text-center">
            Nenhum produto encontrado nesta categoria.
          </p>
        )}
      </section>

      <div className="mt-8 text-center">
        <Link
          href="/"
          className="inline-block bg-blue-500 text-white py-2 px-4 rounded hover:bg-blue-600 transition"
        >
          Voltar ao Início
        </Link>
      </div>
    </main>
  );
}
